@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  box-sizing: border-box;
  margin: 0;
  font-family: "Poppins", sans-serif;
}

.header {
  width: 100%;
  background-color: white;
  position: fixed;
  top: 0;
  z-index: 10;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 10px 80px;
  box-sizing: border-box;
  border-bottom: 2px solid rgb(224, 220, 220);
}

.left-navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 50%;
}

.left-navbar a {
  text-decoration: none;
  margin: 0 30px;
  color: #000;
  margin: 0 30px;
  font-size: 16px;
}

.left-navbar a h3 {
  color: rgb(0, 150, 255);
  font-size: 2.1em;
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.08;
  letter-spacing: -0.9px;
  margin: 0;
}

.left-navbar a p {
  object-fit: contain;
  font-size: 1em;
  font-weight: 400;
  font-style: italic;
  font-stretch: normal;
  line-height: 0.8;
  letter-spacing: 0.9px;
  color: #000;
  margin: 0;
}

.categories-href {
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: 0.9px;
}

.right-navbar {
  display: -webkit-flex;
  display: flex;
  width: 50%;
  -webkit-align-items: center;
          align-items: center;
}

.right-navbar p {
  min-width: 23em;
  opacity: 0.6;
  font-size: 15px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1;
  letter-spacing: 0.4px;
  color: rgba(0, 0, 0, 0.6);
  margin-left: 20px;
  padding-left: 100px;
}

.right-navbar a {
  text-decoration: none;
  background-image: linear-gradient(269deg, rgb(0, 72, 255), rgb(0, 150, 255));
  color: white;
  font-size: 12px;
  padding: 12px 25px;
  border-radius: 50px;
  margin-left: 20px;
}

.search-div {
  margin-top: 100px;
  padding: 10px 40px;
}

.search-div input {
  padding: 5px 8px;
  border-radius: 6px;
  outline: unset;
  width: 30vw;
}

.search-div button {
  margin-left: 10px;
  background-color: rgb(51, 51, 248);
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 7px 10px;
  cursor: pointer;
}

.search-div button:hover {
  background-color: rgb(55, 55, 94);
}

.capsule-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  padding: 0 70px 0px;
  margin-top: 120px;
}

.capsule {
  background-color: white;
  border: 1px solid #0096ff;
  border-radius: 50px;
  box-sizing: border-box;
  color: #0096ff;
  cursor: pointer;
  margin: 10px;
  padding: 10px 20px;
}

.blog-wrapper {
  padding: 0 35px;
  margin-top: 25px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.blog {
  width: 22%;
  border: 1px solid #0096ff;
  border-radius: 8px;
  margin: 10px 17px;
}

.blog img {
  width: 100%;
  height: 186px;
  border-radius: 8px;
}

.blog-data {
  padding: 10px;
}

.blog-data h4 {
  margin: 0;
  font-size: 1.25em;
  font-weight: 600;
  line-height: 1.14;
  color: #000;
  font-family: Raleway;
}

.blog-data h5 {
  margin: 0;
  font-size: 0.9em;
  font-weight: 500;
  line-height: 2;
  color: #0096ff;
  font-family: Raleway;
}

.blog-data p {
  margin: 0;
  opacity: 0.5;
  font-family: Raleway;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.43;
  letter-spacing: 0.35px;
  color: #000;
}

